<template>
  <div v-if="topic" class="topic">
    <base-panel>
      <template #panel-content>
        <div class="topic-head">
          <div class="head-top">
            <el-tag
              v-if="topic.top || topic.good"
              size="small"
              type="success"
              effect="dark"
              >{{ topic.top ? "置顶" : "精华" }}</el-tag
            >
            <el-tag size="small" v-else type="info">{{
              topic.tab | tabTextTransform
            }}</el-tag>
            <h3>{{ topic.title }}</h3>
            <el-button
              @click="collect"
              v-if="accesstoken"
              type="success"
              class="scyqx"
              >{{ topic.is_collect ? "取消收藏" : "收藏" }}</el-button
            >
          </div>
          <p>
            <span class="iconfont icon-dian dics"></span>
            <span>发布于{{ topic.create_at | fromNow }}</span>
            <span class="iconfont icon-dian"></span>
            <span>作者{{ topic.author.loginname }}</span>
            <span class="iconfont icon-dian dics"></span>
            <span>{{ topic.visit_count }}次浏览</span>
            <span class="iconfont icon-dian dics"></span>
            <span>来自{{ topic.tab | tabTextTransform }}</span>
          </p>
        </div>
        <div class="toppic-content" v-html="topic.content"></div>
      </template>
    </base-panel>
    <!-- 评论区 -->
    <base-panel v-if="topic.replies.length">
      <template #panel-head>
        <div class="comment-head">
          <span
            ><span>{{ topic.replies.length }}</span> 回复</span
          >
        </div>
      </template>
      <!-- 评论 -->
      <template #panel-content>
        <div class="comment-list">
          <div
            class="comment-item"
            v-for="(comment, index) in topic.replies"
            :key="comment.id"
            :class="[
              'comment-time',
              { active: commentLikeMost.includes(comment.id) },
            ]"
          >
            <img :src="comment.author.avatar_url" alt="" /><!-- 头像 -->
            <div class="comment-content">
              <span class="info">
                <span>
                  {{ comment.author.loginname }} {{ index + 1 }}楼
                  <span class="iconfont icon-dian dics"></span>
                  {{ comment.create_at | fromNow }}
                </span>
                <!-- 点赞 -->
                <span class="like" v-if="accesstoken || comment.ups.length">
                  <!-- 将评论的 comment 传递过来 -->
                  <span
                    @click="like(comment)"
                    :class="['icon-zan1', 'iconfont', { up: comment.is_uped }]"
                  ></span>
                  <span v-if="comment.ups.length">{{
                    comment.ups.length
                  }}</span>
                </span>
              </span>
              <span class="text" v-html="comment.content"></span>
            </div>
          </div>
        </div>
      </template>
    </base-panel>
    <!-- 添加评论 -->
    <!-- 评论框只在登录状态下显示  使用 v-if 进行判断 -->
    <BasePanel v-if="accesstoken">
      <template #panel-head>添加评论</template>
      <template #panel-content>
        <div class="markdown-container">
          <div class="container">
            <mavon-editor ref="md" style="min-height: 400px" v-model.trim="commenText"/>
            <el-button class="editor-btn" type="primary" @click="reply">提交</el-button>
          </div>
        </div>
      </template>
    </BasePanel>
  </div>
</template>

<script>
import BasePanel from "../components/layout/BasePanel.vue";
import { getTopic, collected, de_collected, zan,reply} from "../plugins/api";
export default {
  components: { BasePanel },
  props: ["topicId"],
  data() {
    return {
      topic: null,
      accesstoken: "",
      commenText: '',
      // 评论框设置
      markdownOption: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
    };
  },
  computed: {
    commentLikeMost() {
      // 判断是否有评论
      if (this.topic.replies.length === 0) {
        return [];
      }
      // 对评论数组的点赞个数进行排序
      const arr = this.topic.replies
        .slice(0)
        .sort((a, b) => b.ups.length - a.ups.length);
      // 存储点赞的id
      const commentIds = [];
      // 整合点赞最多的数组有哪些
      const maxOne = arr[0].ups.length;
      // 对整合后的点赞个数进行判断
      if (maxOne > 2) {
        // 筛选出第一大的点赞数
        const oneArr = arr.filter((item) => item.ups.length === maxOne);
        oneArr.forEach((comment) => {
          commentIds.push(comment.id);
        });
        // 对第二大点赞个数进行整合
        const maxTwo = arr[oneArr.length].ups.length;
        // 对整合后的点赞个数进行判断
        if (maxTwo > 2) {
          // 筛选出第二大的点赞数
          const twoArr = arr.filter((item) => item.ups.length === maxTwo);
          twoArr.forEach((comment) => {
            commentIds.push(comment.id);
          });
        }
      }
      return commentIds;
    },
  },
  // 主题详情
  async created() {
    const accesstoken = sessionStorage.getItem("accesstoken");
    if (accesstoken) {
      // 判断accesstoken 是否存在
      this.accesstoken = accesstoken;
    }
    const res = await getTopic(this.topicId, accesstoken || null);
    this.topic = res.data;
  },
  // 收藏与取消收藏
  methods: {
    async collect() {
      const accesstoken = this.accesstoken;
      if (this.topic.is_collect) {
        // 取消
        await de_collected(this.topic.id, accesstoken);
        this.topic.is_collect = false;
      } else {
        // 收藏
        await collected(this.topic.id, accesstoken);
        this.topic.is_collect = true;
      }
    },
    async like(comment) {
      // 不能对自己的评论点赞
      const accesstoken = this.accesstoken;
      if (accesstoken) {
        // 发送网络请求
        const res = await zan(comment.id, accesstoken);
        const id = JSON.parse(sessionStorage.getItem("userInfo")).id;
        if (res.action === "up") {
          // 点赞成功
          // 修改对应评论的 is_uped 以及 ups
          comment.is_uped = true;
          comment.ups.push(id);
        } else {
          // 取消点赞成功
          // 修改对应评论的 is_uped 以及 ups
          comment.is_uped = false;
          comment.ups = comment.ups.filter((item) => item !== id);
        }
      }
    },
    async reply(){  // 这里的 reply 是点击事件
      const {commenText, accesstoken, topic} = this
      if(commenText){
        await reply(topic.id, accesstoken, commenText) // 这里的reply是传递过来
        // 评论的请求成功之后，后端返回的信息太少是无法更新本地的，所以我们重新请求整个
        // 文章数据来更新
       const res =  await getTopic(this.topicId, accesstoken);
       this.topic = res.data;
       this.commenText= ''
      }
    }
  },
};
</script>

<style lang='less'>
.topic {
  // 评论区
  .markdown-container {
    .editor-btn {
      margin-top: 20px;
    }
    .title {
      padding-bottom: 30px;
      text-align: center;
      font-size: 20px;
      letter-spacing: 1px;
      color: #333;
      font-weight: 600;
    }
  }
  .topic-head {
    background-color: rgb(255, 255, 255);
    padding: 20px;
    border-bottom: 1px solid #ccc;
    .head-top {
      display: flex;
      align-items: center;
      h3 {
        margin-left: 20px;
        font-size: 30px;
      }
      .scyqx {
        margin-left: 60%;
        margin-top: 4px;
      }
    }
    p {
      margin-top: 4px;
      font-size: 12px;
      display: flex;
      align-items: center;
      .disc {
        font-size: 20px;
        margin-top: 10px;
      }
    }
  }
  .toppic-content {
    background-color: rgb(255, 255, 255);
    line-height: 1.5;
    padding: 0 19px;
    img {
      width: 80%;
    }
  }
  // 评论区
  .comment-head {
    line-height: 40px;
    padding-left: 10px;
  }
  .comment-list {
    .comment-item {
      display: flex;
      align-items: flex-start;
      border-bottom: 1px solid #ccc;
      padding: 10px 10px 20px;
      img {
        width: 40px;
        border-radius: 5px;
      }
      .comment-content {
        display: flex;
        flex-grow: 1; //放大
        flex-direction: column;
        margin-left: 15px;
        .info {
          display: flex;
          justify-content: space-between;
          .like {
            cursor: pointer;
          }
          .up {
            color: rgb(255, 0, 0);
          }
        }
      }
    }
    .comment-item.active {
      background-color: rgb(236, 234, 234);
    }
  }
}
</style>